import React from 'react';
import {InfoRow, Cell} from '@vkontakte/vkui';
import '@vkontakte/vkui/dist/vkui.css';
import achiev_1 from '../img/achiev-1.png';

//компонент достижения
class Achievment extends React.Component {
	constructor(props) {
    super(props);
	}
  render() {
  	return(
       <Cell indicator={this.props.lvlNum + 'lvl'}>
          <InfoRow title={this.props.title}>
            <img src={achiev_1} />
          </InfoRow>
       </Cell>
    );
  }

}

export default Achievment;